<template>
  <div style="margin: auto; width: 800px;">
    <h1>{{ msg }}</h1>
      <h2>keywords</h2>

      <template v-for="w in sortedWords">
      <span class="cloud">      
          <div class="word"
          v-bind:style="{color: '#' + getColor(w.normalizedRating), fontSize: (w.normalizedCount * maxFontSize) + 'px'}">
            {{w.text}}
          </div> 
      </span>                   
      </template>
            
      <h2>benchmark</h2>
            

      <template v-for="w in sortedWords">
      <span class="cloud">      
          <div class="word"
          v-bind:style="{color: '#' + getColor(w.normalizedBenchmarkRating), fontSize: (w.normalizedBenchmarkCount * maxFontSize) + 'px'}">
            {{w.text}}
          </div>        
      </span>            
      </template>            
  </div>
</template>

<script>
export default {
  props: ['words'],
  name: 'wordcloud',
  data () {
    return {
      msg: 'Word Cloud',
      maxFontSize: 100
    }
  },
  methods: {
    getColor: function (ratio) {
      var color1 = 'FF0000'
      var color2 = '00FF00'
      var hex = function (x) {
        x = x.toString(16)
        return (x.length === 1) ? '0' + x : x
      }

      var r = Math.ceil(parseInt(color1.substring(0, 2), 16) * ratio + parseInt(color2.substring(0, 2), 16) * (1 - ratio))
      var g = Math.ceil(parseInt(color1.substring(2, 4), 16) * ratio + parseInt(color2.substring(2, 4), 16) * (1 - ratio))
      var b = Math.ceil(parseInt(color1.substring(4, 6), 16) * ratio + parseInt(color2.substring(4, 6), 16) * (1 - ratio))

      return hex(r) + hex(g) + hex(b)
    }
  },
  computed: {
    sortedWords: function () {
      this.words.sort(function (a, b) {
        if (a.text < b.text) return -1
        if (a.text > b.text) return 1
        return 0
      })

      // normalize count
      var maxCount = 0
      var maxBenchmarkCount = 0
      this.words.forEach(function (x) {
        if (x.count > maxCount) {
          maxCount = x.count
        }
        if (x.benchmark_count > maxBenchmarkCount) {
          maxBenchmarkCount = x.benchmark_count
        }
      })

      return this.words.map(function (x) {
        x.normalizedCount = x.count / maxCount
        x.normalizedBenchmarkCount = x.benchmark_count / maxBenchmarkCount

        x.normalizedRating = x.rating / 5
        x.normalizedBenchmarkRating = x.benchmark_rating / 5
        return x
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}

.cloud {
  // width: 700px;
  // background: lightyellow;
}

.word {
  padding: 0px;
  display: inline-block;
  border: 2px;
  // background: lightgray;
  // height: 200px;
  // vertical-align: text-bottom;
  
}

.flipped {
  padding: 5px;
  // display: inline-block;
  border: 2px;
  background: lightblack;
  vertical-align: text-bottom;   
  min-height: 200px;
     

}

</style>



